import ErmjBaseComponent from "../component/ErmjBaseComponent";
import ErmjPathHelper, { ErmjAudioConst } from "../data/ErmjPathHelper";
import ErmjMatchPlayerView from "../subView/ErmjMatchPlayerView";
import ErmjDefine from "../data/ErmjDefine";
import ErmjGameStartView from "../subView/ErmjGameStartView";
import ErmjAskNoticeView from "../subView/ErmjAskNoticeView";
import ErmjLeftTipsView from "../subView/ErmjLeftTipsView";
import ErmjFlowView from "../subView/ErmjFlowView";
import ErmjSettleView from "../subView/ErmjSettleView";
import ErmjOutMjViewManager from "../manager/ErmjOutMjViewManager";
import ErmjPlayerView from "../subView/player/ErmjPlayerView";
import ErmjMjPlayerView from "../subView/mahjong/ErmjMjPlayerView";
import ErmjMahjongHillView from "../subView/mahjong/ErmjMahjongHillView";
import ErmjRuleConst from "../data/ErmjRuleConst";
import ErmjGameConst from "../data/ErmjGameConst";
import ErmjMenuView from "../subView/ErmjMenuView";
import ErmjGameEvent from "../data/ErmjGameEvent";
import ErmjAskActionView from "../subView/ErmjAskActionView";
import ErmjMahjongSelfPlayView from "../subView/ErmjMahjongSelfPlayView";
import ErmjDiceAnim from "../component/ErmjDiceAnim";
import ErmjRubMjAnim from "../component/ErmjRubMjAnim";
import ErmjWinLightningView from "../subView/ErmjWinLightningView";
import ErmjMjStyleHelper from "../tool/ErmjMjStyleHelper";
import ErmjAskBtnView from "../subView/ErmjAskBtnView";
import ErmjDriver from "../ErmjDriver";

const { ccclass, property } = cc._decorator;
@ccclass
export default class ErmjMainUI extends ErmjBaseComponent {
    public viewSet: ViewSet;
    private Define: ErmjDefine;
    private outMjManager: ErmjOutMjViewManager;

    private playerViewList: ErmjPlayerView[] = [];
    private mjPlayerViewList: ErmjMjPlayerView[] = [];
    private levelSp: cc.Sprite;
    private baseLbl: cc.Label;
    private lastOutSign: cc.Node;
    // 子View
    public matchPlayerView: ErmjMatchPlayerView;
    public gameStartView: ErmjGameStartView;
    public lightningView: ErmjWinLightningView;
    public askNoticeView: ErmjAskNoticeView;
    public mjHillView: ErmjMahjongHillView;
    public leftTipsView: ErmjLeftTipsView;
    public askActionView: ErmjAskActionView;
    public flowView: ErmjFlowView;
    public settleView: ErmjSettleView;
    public selfPlayView: ErmjMahjongSelfPlayView;
    public askBtnView: ErmjAskBtnView;

    public headTipsManager: HeadTipsManager;
    public taskManager: TaskManager;
    // 组件
    public diceAnimComp: ErmjDiceAnim;
    public rubMjAnimComp: ErmjRubMjAnim;

    protected onLoad() {
        this.viewSet = new ViewSet();
        this.Define = ErmjDriver.instance.Define;
        this.initView();
    }

    protected onEnable() {
        Game.Event.on(ErmjGameEvent.doPatchwork, this, this.doHillPatchwork);
        Game.Event.on(ErmjGameEvent.doTingCallPlay, this, this.doTingSwitch);
        Game.Event.on(ErmjGameEvent.onUserInfoTouch, this, this.onUserInfoShow);
        Global.Event.on(GlobalEvent.TYPE_VIP_ENTER_GAME, this, this.vipEnterGame);
    }

    protected onDisable() {
        Game.Event.off(ErmjGameEvent.doPatchwork, this, this.doHillPatchwork);
        Game.Event.off(ErmjGameEvent.doTingCallPlay, this, this.doTingSwitch);
        Game.Event.off(ErmjGameEvent.onUserInfoTouch, this, this.onUserInfoShow);
        Global.Event.off(GlobalEvent.TYPE_VIP_ENTER_GAME, this, this.vipEnterGame);
    }

    private vipEnterGame(msg) {
        if (msg.data._gid == ErmjGameConst.Gid && msg.data._glv === Game.Control.curLv){
            Global.Event.event(GlobalEvent.VIPADMISSION, msg.data);
        }
    }

    protected onDestroy() {
        this.taskManager.onDispose();
    }

    private initView() {
        this.node.width = cc.Canvas.instance.node.width;
        this.node.height = cc.Canvas.instance.node.height;

        this.initManager();

        this.initCenterView();
        this.initBottomView();
        this.initTopView();
        this.initFloatView();

        this.initHeadTipsManager();
        this.initTaskManager();

        this.levelSp = cc.find("bottomNode/levelSp", this.node).getComponent(cc.Sprite);
        this.baseLbl = cc.find("bottomNode/baseScore/baseLbl", this.node).getComponent(cc.Label);
        this.lastOutSign = cc.find("centerNode/outSign", this.node);
        this.lastOutSign.active = false;

        let menuNode = cc.find("menuNode", this.node);
        Global.Toolkit.freeAdjustIphoneX([menuNode]);
        let menuView = new ErmjMenuView(menuNode);
        menuView.active = true;
        this.registView(this.Define.ViewMenu, menuView);

        let paomadengNode: cc.Node = cc.find("topNode/paomadeng", this.node);
        let paomadengMask: cc.Node = cc.find("MsgBox", paomadengNode);
        Global.UIHelper.addPaoMaDengComp(paomadengMask, false, paomadengNode);
        Global.UIHelper.addWifiComp(this.getChild('rightAttach/wifiNode'), 2);

        Global.UIHelper.addAdmissionComp(cc.find("topNode/admissionBox", this.node));

        ErmjGameConst.addCommonClick(this.node, "testBtn", () => {
            Game.Event.event(Game.EVENT_CALL_RECONNECT);
        }, this);
    }

    private initManager() {
        // 打出麻将子管理
        let outRoot = this.getChild("centerNode/mahjong/outMjPool");
        this.outMjManager = new ErmjOutMjViewManager(outRoot);
        ErmjDriver.instance.outMjManager = this.outMjManager;
    }

    private initCenterView() {
        let root = this.getChild("centerNode");

        // 玩家
        for (let i = 0; i < ErmjGameConst.maxPlayerCount; i++) {
            let node = this.getChild(`centerNode/player/player${i}`);
            let player = new ErmjPlayerView(node, i);
            player.clientSit = i;
            player.hide();
            this.playerViewList.push(player);
        }

        // 玩家手牌
        for (let i = 0; i < ErmjGameConst.maxPlayerCount; i++) {
            let node = this.getChild(`centerNode/mahjong/mahjong${i}`);
            let mjPlayer = new ErmjMjPlayerView(node, i);
            mjPlayer.active = false;
            this.mjPlayerViewList.push(mjPlayer);
        }

        // 自己手牌麻将操作
        let selfPlayPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/selfPlayView", cc.Prefab);
        let selfPlayNode = <cc.Node>cc.instantiate(selfPlayPrefab);
        selfPlayNode.setParent(root.getChildByName("selfRoot"));
        selfPlayNode.setPosition(cc.Vec2.ZERO);
        this.selfPlayView = new ErmjMahjongSelfPlayView(selfPlayNode);
        this.selfPlayView.active = false;
        this.registView(this.Define.ViewSelfPlay, this.selfPlayView);

        // 操作按钮
        let askActionPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/askActionView", cc.Prefab);
        let askActionNode = <cc.Node>cc.instantiate(askActionPrefab);
        askActionNode.setParent(root);
        askActionNode.setPosition(cc.Vec2.ZERO);
        this.askActionView = new ErmjAskActionView(askActionNode);
        this.askActionView.active = false;
        this.registView(this.Define.ViewAskAction, this.askActionView);

        // 挂机按钮
        let askBtnPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/autoBtnVIew", cc.Prefab);
        let askBtnNode = <cc.Node>cc.instantiate(askBtnPrefab);
        askBtnNode.setParent(root);
        askBtnNode.setPosition(cc.Vec2.ZERO);
        this.askBtnView = new ErmjAskBtnView(askBtnNode);
        this.askBtnView.active = true;
        this.registView(this.Define.ViewAskBtn, this.askBtnView);
    }

    private initBottomView() {
        let root = this.getChild("bottomNode");

        // 操作轮盘提示
        let askNoticePrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/askNoticeView", cc.Prefab);
        let askNoticeNode = <cc.Node>cc.instantiate(askNoticePrefab);
        askNoticeNode.setParent(root);
        askNoticeNode.setPosition(cc.Vec2.ZERO);
        this.askNoticeView = new ErmjAskNoticeView(askNoticeNode);
        this.askNoticeView.active = true;
        this.registView(this.Define.ViewAskNotice, this.askNoticeView);

        // 牌墙
        let mjHillPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/mahjong/mahjongHillView", cc.Prefab);
        let mjHillNode = <cc.Node>cc.instantiate(mjHillPrefab);
        mjHillNode.setParent(root);
        mjHillNode.setPosition(cc.Vec2.ZERO);
        this.mjHillView = new ErmjMahjongHillView(mjHillNode);
        this.mjHillView.active = false;
        this.registView(this.Define.ViewMjHill, this.mjHillView);

        // 骰子动画组件 
        let diceAnimPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/diceAnim", cc.Prefab);
        let diceAnimNode = <cc.Node>cc.instantiate(diceAnimPrefab);
        diceAnimNode.setParent(root);
        diceAnimNode.setPosition(cc.Vec2.ZERO);
        this.diceAnimComp = diceAnimNode.getComponent(ErmjDiceAnim);
        this.diceAnimComp.node.active = false;
    }

    private initTopView() {
        let root = this.getChild("topNode");

        // 剩余提示
        let leftTipsNode = cc.find("leftTipsView", root);
        this.leftTipsView = new ErmjLeftTipsView(leftTipsNode);
        this.leftTipsView.active = false;
        this.registView(this.Define.ViewLeftTips, this.leftTipsView);

        // 搓麻将子动画组件 
        let rubMjAnimPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/rubMjAnim", cc.Prefab);
        let rubMjAnimNode = <cc.Node>cc.instantiate(rubMjAnimPrefab);
        rubMjAnimNode.setParent(root);
        rubMjAnimNode.setPosition(cc.Vec2.ZERO);
        this.rubMjAnimComp = rubMjAnimNode.getComponent(ErmjRubMjAnim);
        this.rubMjAnimComp.node.active = false;
    }

    private initFloatView() {
        let root = this.getChild("floatNode");

        // 匹配中
        let matchPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/matchPlayerView", cc.Prefab);
        let matchNode = <cc.Node>cc.instantiate(matchPrefab);
        matchNode.setParent(root);
        matchNode.setPosition(cc.Vec2.ZERO);
        this.matchPlayerView = new ErmjMatchPlayerView(matchNode);
        this.matchPlayerView.active = false;
        this.registView(this.Define.ViewMatchPlayer, this.matchPlayerView);

        // 开始游戏
        let startPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/gameStartView", cc.Prefab);
        let startNode = <cc.Node>cc.instantiate(startPrefab);
        startNode.setParent(root);
        startNode.setPosition(cc.Vec2.ZERO);
        this.gameStartView = new ErmjGameStartView(startNode);
        this.gameStartView.active = false;
        this.registView(this.Define.ViewGameStart, this.gameStartView);

        // 胡牌闪电
        let lightningPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/winLightningView", cc.Prefab);
        let lightningNode = <cc.Node>cc.instantiate(lightningPrefab);
        lightningNode.setParent(root);
        lightningNode.setPosition(cc.Vec2.ZERO);
        this.lightningView = new ErmjWinLightningView(lightningNode);
        this.lightningView.active = false;
        this.registView(this.Define.ViewLightning, this.lightningView);

        // 流局
        let flowPrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/flowView", cc.Prefab);
        let flowNode = <cc.Node>cc.instantiate(flowPrefab);
        flowNode.setParent(root);
        flowNode.setPosition(cc.Vec2.ZERO);
        this.flowView = new ErmjFlowView(flowNode);
        this.flowView.active = false;
        this.registView(this.Define.ViewFlow, this.flowView);

        // 结算
        let settlePrefab = Global.ResourceManager.getBundleRes(ErmjGameConst.Gid, ErmjPathHelper.gamePrefabsPath + "panel/subView/settleView", cc.Prefab);
        let settleNode = <cc.Node>cc.instantiate(settlePrefab);
        settleNode.setParent(root);
        settleNode.setPosition(cc.Vec2.ZERO);
        this.settleView = new ErmjSettleView(settleNode);
        this.settleView.active = false;
        this.registView(this.Define.ViewSettle, this.settleView);
    }

    private initHeadTipsManager(){
        this.headTipsManager = new HeadTipsManager();
        this.headTipsManager.init(cc.find('floatNode', this.node), cc.find('interactView', this.node), false);
    }

    private initTaskManager(){
        this.taskManager = new TaskManager();
        this.taskManager.init(cc.find('rightAttach/taskRoot', this.node), ErmjGameConst.Gid, -1);
        this.taskManager.reqGetGameTaskList();
    }

    public doResetMj() {
        this.selfPlayView.resetSelectMj();
    }

    /** 更新底分数值 */
    public updateLevelBase(base: number, lv: string) {
        Global.ResourceManager.loadBundleAutoAtlas(ErmjGameConst.Gid, this.levelSp, ErmjPathHelper.gameTexturePath + "dynamic/atlas_dynamic", ErmjGameConst.LevelSpCfg[lv], null, true);
        this.baseLbl.string = Global.Toolkit.formatPointStr(base);
    }

    public showMatch(timeo: number) {
        this.matchPlayerView.active = true;
        let player = this.playerViewList[0];
        player.show(Global.PlayerData);         // debug 没有area数据

        if (timeo && timeo > 0) {
            this.matchPlayerView.setTimeRunConfig(timeo, () => {
                ErmjDriver.instance.Context.isWaitMatch = false;
                this.matchPlayerView.active = false;
                Global.UI.showYesNoBox('匹配超时，是否重新匹配？', () => {
                    Game.Control.trySendEnter({ "_from": "jump" });    // 断socket则不能用这个
                }, () => {
                    Game.Server.send(this.Define.CmdLeave, { "IsClose": 1 });
                    ErmjDriver.instance.leaveGame();
                });
            }, this);
        }
    }

    public onBanker(bankerSeat: number, dice: number[], isAnim: boolean) {
        let player = this.playerViewList[bankerSeat];
        if (player) {
            player.showBanker(true);
        }
        let [whoStart, indexInWall] = ErmjRuleConst.calculateDealBeigin(bankerSeat, dice);
        this.mjHillView.active = false;         // 暂关闭不用了
        this.mjHillView.initWallDeal(whoStart, indexInWall);

        this.leftTipsView.active = true;
        this.leftTipsView.updateLeftLbl(ErmjGameConst.mahjongTotal);
        // 骰子动画
        this.diceAnimComp.node.active = true;
        this.diceAnimComp.showDice(dice, isAnim);
        if (isAnim) {
            ErmjGameConst.playSound(ErmjAudioConst.commonAudio.Dice, true);
            Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onBanker", 2);   // 摇骰子动画时间
        }
    }

    /**
     * 发牌动画
     * @param userCardsMap [key本地座位]-[value手牌数据对象]的map
     * @param bankerSeat 庄家本地座位
     */
    public onDeal(userCardsMap: any, bankerSeat: number, leftCount: number) {
        if (!userCardsMap || Global.Toolkit.isEmptyObject(userCardsMap))
            return;
        let delay = 0;
        let dealOrder = ErmjRuleConst.getDealOrderArr(bankerSeat);
        for (let i = 0; i < ErmjRuleConst.dealConfig.length; i++) {
            let arr = ErmjRuleConst.dealConfig[i];
            for (let j = 0; j < arr.length; j++) {        // j与dealOrder的key对应
                let localSeat = dealOrder[j];
                let count = arr[j];
                let mjPlayer = this.mjPlayerViewList[localSeat];
                if (mjPlayer && count > 0 && userCardsMap[localSeat]) {     // 大于0才动画
                    let cards = userCardsMap[localSeat].cards || [];
                    Game.Component.scheduleOnce(() => {
                        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.DealCard, true);
                        this.mjHillView.doDeal(count);
                        mjPlayer.active = true;
                        mjPlayer.setHandMjShow(true);
                        mjPlayer.dealHandMj(count, cards);
                        if (localSeat == 0) {
                            mjPlayer.setDarkHandMjOutScreen();
                            this.selfPlayView.active = true;
                            this.selfPlayView.dealHandMj(count, cards);
                        }

                        this.leftTipsView.reduceLeftLbl(count);     // 动态减
                    }, delay);
                    delay += 0.35;
                }
            }
        }

        Game.Component.scheduleOnce(() => {
            this.leftTipsView.active = true;
            this.leftTipsView.updateLeftLbl(leftCount);
            this.selfPlayView.sortCardDirectly(false);      // 整理手牌排序
        }, delay);

        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onDeal", delay + 0.5);
    }

    public onDealDirectly(userCardsMap: any, leftCount: number) {
        if (!userCardsMap || Global.Toolkit.isEmptyObject(userCardsMap))
            return;
        this.mjPlayerViewList.forEach((mjPlayer, localSeat) => {
            let data = userCardsMap[localSeat];
            mjPlayer.active = true;
            mjPlayer.setHandMjShow(true);
            mjPlayer.dealHandMj(data.hand_count, []);
            if (localSeat == 0) {
                mjPlayer.setDarkHandMjOutScreen();
                this.selfPlayView.active = true;
                this.selfPlayView.dealHandMj(data.hand_count, data.cards);
            }
        });
        this.leftTipsView.updateLeftLbl(leftCount);
        this.selfPlayView.sortCardDirectly(false);      // 整理手牌排序
    }

    /**
     * 补花
     * @param localSeat 谁补花
     * @param outArr 花牌
     * @param newArr 换进来的牌
     */
    public onChangeFlower(localSeat: number, outArr: number[], newArr: number[], leftCount: number) {
        ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("buhua", localSeat == 0 ? 0 : 1), true);
        let mjPlayer = this.mjPlayerViewList[localSeat];
        mjPlayer.changeFlower(outArr, newArr || []);
        if (localSeat == 0) {
            this.selfPlayView.changeFlower(outArr, newArr || []);
        }
        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onChangeFlower", 1);     // 时间要比changeFlower里的动画总时长长点
        this.leftTipsView.updateLeftLbl(leftCount);
    }

    /**
     * 摸牌
     * @param localSeat 谁摸牌
     * @param cards 摸进来的牌 最多只支持一张牌, 其他人是[]
     * @param isHeaDraw 是否从牌堆头摸
     * @param leftCount 剩余牌
     * @param isRub 是否搓牌
     */
    public onDrawCard(localSeat: number, cards: number[], isHeaDraw: boolean, leftCount: number, isRub: boolean) {
        if (isHeaDraw) {
            this.mjHillView.doDeal(1);
        }
        else {
            this.mjHillView.doPatchwork(1);
        }
        this.leftTipsView.updateLeftLbl(leftCount);
        this.mjPlayerViewList[localSeat].drawHandMj(cards[0]);

        if (localSeat == 0) {
            // 搓牌动画
            if (isRub) {
                this.rubMjAnimComp.node.active = true;
                this.rubMjAnimComp.doRubAnim(cards[0]);
                Game.Component.scheduleOnce(() => {
                    this.rubMjAnimComp.node.active = false;
                    this.selfPlayView.drawHandMj(cards[0]);
                    ErmjGameConst.playSound(ErmjAudioConst.commonAudio.DrawCard, true);
                }, 1.5);
                Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onDrawCard.isRub", 2);
            }
            else {
                this.selfPlayView.drawHandMj(cards[0]);
                ErmjGameConst.playSound(ErmjAudioConst.commonAudio.DrawCard, true);
            }
        }
        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onDrawCard", 0.31);
    }

    /** 出牌 普通听时候也会走这出一张 */
    public onPlay(localSeat: number, value: number, isAuto: boolean = false) {
        ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath(ErmjMjStyleHelper.mjOutSoundMap[value], localSeat == 0 ? 0 : 1), true);
        this.mjPlayerViewList[localSeat].outCard(value)
        if (localSeat == 0) {
            this.selfPlayView.outCard(value, true, isAuto);
        }
        let animTime = this.Define.outMjShowTime + this.Define.outMjFlyTime;
        Game.Component.scheduleOnce(() => {
            this.showLastOutSign(true);
        }, animTime + 0.1);
        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onPlay", animTime + 0.15);
    }

    public onChow(localSeat: number, valueArr: number[], chowCard: number, whoSeat: number) {
        ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("chi", localSeat == 0 ? 0 : 1), true);
        if (localSeat == 0) {
            let chowIndex = valueArr.indexOf(chowCard);
            let tempArr = [...valueArr];
            tempArr.splice(chowIndex, 1);
            this.selfPlayView.hideOperCard(tempArr);
            this.mjPlayerViewList[localSeat].chowCard(valueArr, chowCard);
        }
        else {
            this.mjPlayerViewList[localSeat].chowCard(valueArr, chowCard);
        }
        this.showLastOutSign(false);
        this.mjPlayerViewList[whoSeat].reduceOneOutMj();
        this.outMjManager.recycleLastOutMj();
        this.playerViewList[localSeat].showStateSp(true, ErmjGameConst.StateSpStrCfg.Chow);
        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onChow", 0.8);
    }

    public onPong(localSeat: number, valueArr: number[], whoSeat: number) {
        ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("peng", localSeat == 0 ? 0 : 1), true);
        if (localSeat == 0) {
            this.selfPlayView.hideOperCard(valueArr.slice(0, 2));       // 取两个消失
            this.mjPlayerViewList[localSeat].pongCard(valueArr);
        }
        else {
            this.mjPlayerViewList[localSeat].pongCard(valueArr);
        }
        this.showLastOutSign(false);
        this.mjPlayerViewList[whoSeat].reduceOneOutMj();
        this.outMjManager.recycleLastOutMj();
        this.playerViewList[localSeat].showStateSp(true, ErmjGameConst.StateSpStrCfg.Pong);
        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onPong", 0.8);
    }

    public onKong(localSeat: number, valueArr: number[], type: number, whoSeat: number) {
        if (type == 6) {
            ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("angang", localSeat == 0 ? 0 : 1), true);
        }
        else {
            ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("gang", localSeat == 0 ? 0 : 1), true);
        }
        if (localSeat == 0) {
            let count = type == 6 ? 4 : type == 5 ? 1 : 3;              // 6暗杠: 4; 5碰杠: 1; 4直杠: 3
            this.selfPlayView.hideOperCard(valueArr.slice(0, count));       // 根据类似取
            this.mjPlayerViewList[localSeat].kongCard(type, valueArr);
            this.selfPlayView.showPlayTips(false);
        }
        else {
            this.mjPlayerViewList[localSeat].kongCard(type, valueArr);
        }
        if (type == 4) {
            this.showLastOutSign(false);
            this.mjPlayerViewList[whoSeat].reduceOneOutMj();
            this.outMjManager.recycleLastOutMj();
        }
        this.playerViewList[localSeat].showStateSp(true, ErmjGameConst.StateSpStrCfg.Kong);
        Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onKong", 0.8);
    }

    public onWin(localSeat: number, whoSeat: number, card: number, type: number, isAnim: boolean) {
        let stateStr = type == 1 ? ErmjGameConst.StateSpStrCfg.WinAll : type == 3 ? ErmjGameConst.StateSpStrCfg.RobWin : ErmjGameConst.StateSpStrCfg.Win;
        this.playerViewList[localSeat].showStateSp(true, stateStr);

        this.selfPlayView.doWin();
        this.mjPlayerViewList[localSeat].addWinMjShow(card);
        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.Hu, true);
        if (isAnim) {
            if (type == 3) {     // 抢杠胡
                ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("hu", localSeat == 0 ? 0 : 1), true);
                this.mjPlayerViewList[whoSeat].robbedKongLose(card);
            }
            else if (type == 2) {     // 点炮
                ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("hu", localSeat == 0 ? 0 : 1), true);
                this.showLastOutSign(false);
                this.outMjManager.recycleLastOutMj();           // 隐藏最后一次打出的点炮牌
            }
            else {           // 自摸 
                ErmjGameConst.playSound(ErmjAudioConst.genderSoundPath("zimo", localSeat == 0 ? 0 : 1), true);
                this.mjPlayerViewList[localSeat].winHideLastDraw(card);     // 重连时走reward
                if (localSeat == 0) {
                    this.selfPlayView.winHideLastDraw(card);
                }
            }
            this.lightningView.setLightPoint(this.mjPlayerViewList[localSeat].getLightningWorldPos());
            this.lightningView.active = true;
            Game.Event.event(Game.EVENT_ADDTIMELOCK, "Ermj.onWin", 1);
        }
    }

    /** 最后一次出牌光标 */
    public showLastOutSign(flag: boolean) {
        this.lastOutSign.stopAllActions();
        this.lastOutSign.active = flag;
        if (flag) {
            let lastMjItem = this.outMjManager.getLastOutMj();
            this.lastOutSign.setPosition(this.lastOutSign.parent.convertToNodeSpaceAR(lastMjItem.getAttentionWorldPos()));
            this.lastOutSign.runAction(cc.repeatForever(cc.sequence([
                cc.moveBy(0.4, cc.v2(0, 15)),
                cc.moveBy(0.4, cc.v2(0, -15)),
            ])));
        }
    }

    private doHillPatchwork(count: number) {
        this.mjHillView.doPatchwork(count);
    }

    private doTingSwitch(isTing: boolean, tingData: any) {
        if (this.selfPlayView.active)
            this.selfPlayView.onTingChooseSwitch(isTing, tingData);
    }

    private onUserInfoShow(seat: number, worldPos: cc.Vec3){
        let data = ErmjDriver.instance.Context.playerList[seat];
        if (data){
            this.headTipsManager.showHeadView(true, seat, worldPos, data);
        }
    }

    public clearWnd() {
        //关闭界面单独清理玩家信息
        this.clearPlayers();
        this.clearByGame();
    }

    public clearPlayers() {
        for (let i = 0; i < this.playerViewList.length; i++) {
            this.playerViewList[i].hide();
        }
        for (let i = 0; i < this.mjPlayerViewList.length; i++) {
            this.mjPlayerViewList[i].active = false;
        }
        this.selfPlayView.active = false;
    }

    //-----------------------------接口实现--------------------------------
    public clearByRound() {
        this.diceAnimComp.node.active = false;
        this.rubMjAnimComp.node.active = false;
        this.showLastOutSign(false);
        this.outMjManager.clear();
        this.viewSet.callAll("clearByRound");
        this.callAllPlayers("clearByRound");
        this.callAllMjPlayers("clearByRound");
    }

    public clearByGame() {
        this.outMjManager.clear();
        this.viewSet.callAll("clearByGame");
        this.callAllPlayers("clearByGame");
        this.callAllMjPlayers("clearByGame");
    }

    public registView(key: string, view: any) {
        this.viewSet.registView(key, view);
    }

    public getView<T>(key): T {
        return this.viewSet.getViewEx<T>(key);
    }

    //--------------------------------------player相关--------------------------------------------
    //调用单个player方法
    public callPlayer(index: number, func: string, ...args) {
        let player = this.getPlayer(index);
        if (player == null) {
            Logger.error("找不到player!!!", index);
            // Global.UI.fastTip(`找不到player!!! index: ${index}, func: ${func}`);
            return;
        }
        if (player[func] && player[func].apply) {
            player[func].apply(player, args);
        }
    }

    public callAllPlayers(func: string, ...args) {
        for (let i = 0; i < ErmjGameConst.maxPlayerCount; i++) {
            let player = this.getPlayer(i);
            if (player == null)
                return;
            if (player[func] && player[func].apply) {
                player[func].apply(player, args);
            }
        }
    }

    public getPlayer(index: number) {
        return this.playerViewList[index];
    }

    public getMjPlayer(index: number) {
        return this.mjPlayerViewList[index];
    }

    public callMjPlayer(index: number, func: string, ...args) {
        let player = this.getMjPlayer(index);
        if (player == null) {
            Logger.error("找不到player!!!", index);
            // Global.UI.fastTip(`找不到player!!! index: ${index}, func: ${func}`);
            return;
        }
        if (player[func] && player[func].apply) {
            player[func].apply(player, args);
        }
    }

    public callAllMjPlayers(func: string, ...args) {
        for (let i = 0; i < ErmjGameConst.maxPlayerCount; i++) {
            let player = this.getMjPlayer(i);
            if (player == null)
                return;
            if (player[func] && player[func].apply) {
                player[func].apply(player, args);
            }
        }
    }
}